<template lang="pug">
  .container
    loading(:active.sync='isLoading', :opacity='0.85')
      img(src='@/assets/loading.gif', alt='', srcset='')
      vue-typed-js.justify-content-center.align-items-center(:strings="['']")
        small.font-weight-normal.typing
    table.table.table-border.table-hover.text-center.my-2
      thead
        tr.text-center.bg-ro.text-white
          th(width='5%') 
          th 
          th(width='10%') 
          th(width='10%') 
          th(width='15%') 
          th(width='25%') 
      tbody
        tr(v-for='item in orders', :key='item.id')
          td.align-middle {{item.create_at}}
          td.align-middle {{item.message}}
          td.align-middle {{item.payment_method}}
          td.align-middle(:class="{'bg-success':item.is_paid, 'bg-danger': !item.is_paid}")
            span.text-white(v-if='item.is_paid') 
            span.text-white.font-weight-bold(v-else='') 
          td.align-middle {{item.user.name}}
          td.align-middle
            button.btn.btn-outline-ro(@click="openModel('look', item)")
              font-awesome-icon(:icon="['fas', 'eye']")
              | 
            button.btn.btn-outline-ro.ml-1(@click="openModel('edit', item)")
              font-awesome-icon(:icon="['fas', 'edit']")
              | 
    paginationComponents(:paginationService='pagination', v-on:pageService='getOrders')
    #ordersModal.modal.fade(tabindex='-1', role='dialog', aria-labelledby='exampleModalLabel', aria-hidden='true')
      .modal-dialog.modal-lg(role='document')
        .modal-content.border-0
          .modal-header.bg-dark.text-white
            h5#exampleModalLabel.modal-title
              span {{modelTitle}}
            button.close(type='button', data-dismiss='modal', aria-label='Close')
              span(aria-hidden='true') 
          .modal-body
            .row
              .col-sm-12
                .form-group
                  label(for='title') 
                  input#title.form-control-plaintext(type='text', placeholder='', v-model='tempOrders.create_at', readonly='')
                .form-row
                  .form-group.col-md-6
                    label(for='category') 
                    select#category.form-control(v-model='tempOrders.is_paid')
                      option(value='', disabled='') ----
                      option(value='true') 
                      option(value='false') 
                  .form-group.col-md-6
                    label(for='total') 
                    input#total.form-control(type='number', placeholder='', v-model='tempOrders.total')
                hr
                .form-group
                  h5 
                  .custom-control.custom-radio.custom-control-inline
                    input#customRadio1.custom-control-input(type='radio', name='customRadio', value='', v-model='tempOrders.payment_method')
                    label.custom-control-label(for='customRadio1') 
                  .custom-control.custom-radio.custom-control-inline
                    input#customRadio2.custom-control-input(type='radio', name='customRadio', value='', v-model='tempOrders.payment_method')
                    label.custom-control-label(for='customRadio2') 
                  .custom-control.custom-radio.custom-control-inline
                    input#customRadio3.custom-control-input(type='radio', name='customRadio', value='0', v-model='tempOrders.payment_method')
                    label.custom-control-label(for='customRadio3') 0
                  .custom-control.custom-radio.custom-control-inline
                    input#customRadio4.custom-control-input(type='radio', name='customRadio', value='', v-model='tempOrders.payment_method')
                    label.custom-control-label(for='customRadio4') 
                  hr
                  h5 
                  .custom-control.custom-radio.custom-control-inline
                    input#customRadio5.custom-control-input(type='radio', name='customRadio', value='', v-model='tempOrders.payment_method')
                    label.custom-control-label(for='customRadio5') 
                  .custom-control.custom-radio.custom-control-inline
                    input#customRadio6.custom-control-input(type='radio', name='customRadio', value='ATM', v-model='tempOrders.payment_method')
                    label.custom-control-label(for='customRadio6') ATM
                  .custom-control.custom-radio.custom-control-inline
                    input#customRadio7.custom-control-input(type='radio', name='customRadio', value='', v-model='tempOrders.payment_method')
                    label.custom-control-label(for='customRadio7') 
                  hr
                  h5 
                  .custom-control.custom-radio.custom-control-inline
                    input#customRadio8.custom-control-input(type='radio', name='customRadio', value='LINE PAY', v-model='tempOrders.payment_method')
                    label.custom-control-label(for='customRadio8') LINE PAY
                  .custom-control.custom-radio.custom-control-inline
                    input#customRadio9.custom-control-input(type='radio', name='customRadio', value='APPLE PAY', v-model='tempOrders.payment_method')
                    label.custom-control-label(for='customRadio9') APPLE PAY
                  .custom-control.custom-radio.custom-control-inline
                    input#customRadio10.custom-control-input(type='radio', name='customRadio', value='Google PAY', v-model='tempOrders.payment_method')
                    label.custom-control-label(for='customRadio10') Google PAY
                  .custom-control.custom-radio.custom-control-inline
                    input#customRadio11.custom-control-input(type='radio', name='customRadio', value='Pi PAY', v-model='tempOrders.payment_method')
                    label.custom-control-label(for='customRadio11') Pi PAY
                  .custom-control.custom-radio.custom-control-inline
                    input#customRadio12.custom-control-input(type='radio', name='customRadio', value='Samsung PAY', v-model='tempOrders.payment_method')
                    label.custom-control-label(for='customRadio12') Samsung PAY
                  .custom-control.custom-radio.custom-control-inline
                    input#customRadio13.custom-control-input(type='radio', name='customRadio', value='', v-model='tempOrders.payment_method')
                    label.custom-control-label(for='customRadio13') 
                  .custom-control.custom-radio.custom-control-inline
                    input#customRadio14.custom-control-input(type='radio', name='customRadio', value='', v-model='tempOrders.payment_method')
                    label.custom-control-label(for='customRadio14') 
                  .custom-control.custom-radio.custom-control-inline
                    input#customRadio15.custom-control-input(type='radio', name='customRadio', value='', v-model='tempOrders.payment_method')
                    label.custom-control-label(for='customRadio15') 
                  hr
                .form-row
                  .form-group.col-md-4
                    label(for='category') 
                    input#total.form-control(type='text', placeholder='', v-model='tempOrders.user.name')
                  .form-group.col-md-4
                    label(for='total') 
                    input#total.form-control(type='number', placeholder='', v-model='tempOrders.user.tel')
                  .form-group.col-md-4
                    label(for='total') Email
                    input#total.form-control(type='email', placeholder='Email', v-model='tempOrders.user.email')
                .form-group
                  label(for='description') 
                  textarea#description.form-control(type='text', v-model='tempOrders.message')
            .form-group
              .form-check
                input#is_paid.form-check-input(type='checkbox', v-model='tempOrders.is_paid', :true-value='true', :false-value='false')
                label.form-check-label(for='is_paid')
                  | 
          .modal-footer
            button.btn.btn-outline-secondary(type='button', data-dismiss='modal') 
            button.btn.btn-primary(type='button', @click='updataOrders()')
              font-awesome-icon(:icon="['fas', 'spinner']", v-if='status.loadingItem', spin='')
              | 
    #ordersLookModal.modal.fade(tabindex='-1', role='dialog', aria-labelledby='exampleModalLabel', aria-hidden='true')
        .modal-dialog.modal-lg(role='document')
          .modal-content.border-0
            .modal-header.bg-dark.text-white
              h5#exampleModalLabel.modal-title
                span {{modelTitle}}
              button.close(type='button', data-dismiss='modal', aria-label='Close')
                span(aria-hidden='true') 
            .modal-body
              .row
                .col-sm-12
                  .form-group
                    label(for='title') 
                    input#title.form-control-plaintext(type='text', placeholder='', v-model='tempOrders.create_at', readonly='')
                  .form-group
                    label(for='total') 
                    input#total.form-control(type='number', placeholder='', v-model='tempOrders.total', readonly='')
                  hr
                  .form-group
                    h5 
                    input.form-control(type='text', v-model='tempOrders.payment_method', readonly='')
                  .form-row
                    .form-group.col-md-4
                      label(for='category') 
                      input#total.form-control(type='text', placeholder='', v-model='tempOrders.user.name', readonly='')
                    .form-group.col-md-4
                      label(for='total') 
                      input#total.form-control(type='number', placeholder='', v-model='tempOrders.user.tel', readonly='')
                    .form-group.col-md-4
                      label(for='total') Email
                      input#total.form-control(type='email', placeholder='Email', v-model='tempOrders.user.email', readonly='')
                  .form-group
                    label(for='description') 
                    textarea#description.form-control(type='text', v-model='tempOrders.message', readonly='')
            .modal-footer
              button.btn.btn-outline-secondary(type='button', data-dismiss='modal') 
</template>

<script>
/* global $ */
import PaginationComponents from './Pagination';

export default {
  data() {
    return {
      orders: [],
      tempOrders: {
        user: {
          name: '',
          tel: '',
          email: '',
        },
      },
      pagination: {},
      modelTitle: '',
      modelStatus: '',
      isLoading: false,
      status: {
        loadingItem: false,
      },
    };
  },
  methods: {
    getOrders(page) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${
        process.env.COUSTOMPATH
      }/admin/orders?page=${page}`;
      vm.isLoading = true;
      vm.$http.get(url).then((response) => {
        if (response.data.success) {
          vm.pagination = response.data.pagination;
          vm.orders = response.data.orders;
          vm.isLoading = false;
        } else if (response.data.message === ', ') {
          vm.$router.push('/login');
          vm.isLoading = false;
        } else {
          vm.$bus.$emit('message:push',
            `(   |||)
            ${response.data.message}`
            , 'danger');
          vm.isLoading = false;
        }
      });
    },
    updataOrders() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${
        process.env.COUSTOMPATH
      }/admin/order/${vm.tempOrders.id}`;
      vm.status.loadingItem = true;
      vm.$http.put(url, { data: vm.tempOrders }).then((response) => {
        if (response.data.success) {
          vm.status.loadingItem = false;
          $('#ordersModal').modal('hide');
          vm.$bus.$emit('message:push',
            '(*)v'
            , 'success');
          vm.getOrders();
        } else {
          vm.status.loadingItem = false;
          vm.$bus.$emit('message:push',
            `(   |||)
            ${response.data.message}`
            , 'danger');
        }
      });
    },
    openModel(status, item) {
      const vm = this;
      switch (status) {
        case 'edit':
          vm.modelTitle = '';
          vm.modelStatus = 'edit';
          $('#ordersModal').modal('show');
          vm.tempOrders = Object.assign({}, item);
          break;
        case 'look':
          vm.modelTitle = '';
          vm.modelStatus = 'delete';
          $('#ordersLookModal').modal('show');
          vm.tempOrders = Object.assign({}, item);
          break;
        default:
          $('#ordersModal').modal('show');
          break;
      }
    },
  },
  components: {
    PaginationComponents,
  },
  created() {
    this.getOrders();
  },
};
</script>



