<template lang="pug">
  section
    loading(:active.sync='isLoading', :opacity='0.85')
      img(src='@/assets/loading.gif', alt='', srcset='')
      vue-typed-js.justify-content-center.align-items-center(:strings="['']")
        small.font-weight-normal.typing
    .container.my-10
      h2.text-ro(v-if="localFilter === ''") 
      h2.text-ro(v-else) 
      swiper.py-5(:options='swiperOption')
        swiper-slide.border.p-2(v-for='item in productsCard', :key='item.id')
          .shop-top.text-center
            .badge.badge-danger.category-top {{item.category}}
            img.shop-img(:src='item.imageUrl')
          .shop-content
            h6
              a.text-ro(href='http://') {{item.title}}
            p.text-left.p-3.text-description
              | {{item.description}}
            .d-flex.justify-content-between
              span.text-ro-dark NT$ {{item.price}}
              del
                small  NT$ {{item.origin_price}}
          .shop-footer
            .d-flex.justify-content-center.align-items-center(v-if='item.is_enabled')
              router-link.btn.btn-outline-ro.w-50(:to="'/detailed/' + item.id")
                font-awesome-icon(:icon="['fas','spinner']", spin='', v-if='status.loadingID === item.id')
                | 
              a.btn.btn-outline-danger.w-50.ml-1(href='#' @click.prevent="addCart(item.id)")
                font-awesome-icon(:icon="['fas','spinner']", spin='', v-if='status.loadingID === item.id')
                | 
            div(v-else)
              a.btn.btn-outline-danger.btn-block.disabled(href='#')
                | 
    .bg-ro-light.d-flex.justify-content-center.align-items-center.flex-column.py-5.text-white.font-weight-bold.h4
      div
        | 
        span.text-ro-dark 
        | 
      div
        .shop-good
          | 
          span.text-ro-dark 
          | 
      div
        | 
        span.text-ro-dark 
        | 
      p.text-center
        | 
        br
        | 
        span.text-ro-dark 
        | 
    .container.my-10
      h2.text-ro 
      swiper.py-5(:options='swiperOption')
        swiper-slide.border.p-2(v-for='item in productsOff', :key='item.id')
          .shop-top.text-center
            .badge.badge-danger.category-top {{item.category}}
            img.shop-img(:src='item.imageUrl')
          .shop-content
            h6
              a.text-ro(href='http://') {{item.title}}
            p.text-left.p-3.text-description
              | {{item.description}}
            .d-flex.justify-content-between
              span.text-ro-dark NT$ {{item.price}}
              del
                small  NT$ {{item.origin_price}}
          .shop-footer
            .d-flex.justify-content-center.align-items-center(v-if='item.is_enabled')
              router-link.btn.btn-outline-ro.w-50(:to="'/detailed/' + item.id")
                | 
              a.btn.btn-outline-danger.w-50.ml-1(href='#' @click.prevent="addCart(item.id)")
                font-awesome-icon(:icon="['fas','spinner']", spin='', v-if='status.loadingID === item.id')
                | 
            div(v-else)
              a.btn.btn-outline-danger.btn-block.disabled(href='#')
                | 
      .text-center
        router-link.btn.btn-ro.text-white(:to="{name: 'Products', params:{ data: '' }}")
          | 
</template>

<style lang="scss" scoped>
.shop-top{
  position: relative;
  .category-top{
    position: absolute;
    top: 10px;
    right: 10px;
  }
}
.shop-content{
  height: 200px;
  .text-description{
    height: 120px;
    overflow-y: auto;
  }
}
.shop-img{
  max-height: 250px;
}
</style>

<script>
import 'swiper/dist/css/swiper.css';
import { swiper, swiperSlide } from 'vue-awesome-swiper';

export default {
  data() {
    return {
      products: [],
      productsOff: [],
      productsCard: [],
      modelTitle: '',
      cacheModelItem: {},
      localFilter: '',
      isLoading: false,
      status: {
        loadingID: '',
      },
      counts: 0,
      swiperOption: {
        slidesPerView: 4,
        spaceBetween: 5,
        speed: 600,
        autoplay: {
          delay: 5000,
        },
        breakpoints:
        {
          992: {
            slidesPerView: 3,
          },
          768: {
            slidesPerView: 2,
          },
          576: {
            slidesPerView: 1,
          },
        },
      },
    };
  },
  methods: {
    getProducts() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.COUSTOMPATH}/products/all`;
      vm.isLoading = true;
      vm.$http.get(url).then((response) => {
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.getProductsOff();
        vm.getProductsCard();
      });
    },
    getProductsOff() {
      const vm = this;
      const data = vm.products;
      data.forEach((item) => {
        if (item.category === '') {
          vm.productsOff.push(item);
        }
      });
    },
    getProductsCard() {
      const vm = this;
      const data = vm.products;
      data.forEach((item) => {
        if (vm.localFilter === '') {
          if (item.category === 'MVP') {
            vm.productsCard.push(item);
          }
        } else if (item.category === vm.localFilter) {
          vm.productsCard.push(item);
        }
      });
    },
    addCart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${
        process.env.COUSTOMPATH
      }/cart`;
      vm.status.loadingID = id;
      const cartContent = {
        product_id: id,
        qty,
      };
      vm.$http.post(url, { data: cartContent }).then((response) => {
        if (response.data.message === '') {
          vm.$bus.$emit('message:push',
            '(*)v'
            , 'success');
          vm.$bus.$emit('cartCreate:push');
          vm.status.loadingID = '';
        } else if (response.data.message === '') {
          vm.status.loadingID = '';
          vm.$bus.$emit('message:push',
            `(   |||)
            ${response.data.message}`
            , 'danger');
        } else {
          vm.status.loadingID = '';
          vm.$bus.$emit('message:push',
            `(   |||)
            ${response.data.message}`
            , 'danger');
        }
      });
    },
  },
  components: {
    swiper,
    swiperSlide,
  },
  created() {
    const vm = this;
    this.getProducts();
    vm.localFilter = JSON.parse(localStorage.getItem('likecategory')) || '';
  },
};
</script>



