<template lang="pug">
  section.p-top
    loading(:active.sync='isLoading', :opacity='0.85')
      img(src='@/assets/loading.gif', alt='', srcset='')
      vue-typed-js.justify-content-center.align-items-center(:strings="['']")
        small.font-weight-normal.typing
    section.container
      .text-center
        .d-flex.justify-content-center.align-items-center.cart-bg
          vue-typed-js(:strings="['~']", :fadeOut='true')
            h3.text-white.typing.cart-text
      .my-5.row.justify-content-center
        .col-md-6
          table.table
            thead
              tr
                th
                th 
                th 
                th 
            tbody
              tr(v-for='item in carts.carts', :key='item.id')
                td.align-middle
                  button.btn.btn-outline-danger.btn-sm(type='button', @click='removeCart(item.id)')
                    font-awesome-icon(:icon="['fas','spinner']", spin='', v-if='status.loadingItem === item.id')
                    font-awesome-icon(:icon="['far','trash-alt']", v-if='status.loadingItem !== item.id')
                td.align-middle
                  | {{ item.product.title }}
                  .text-success(v-if='item.coupon') 
                td.align-middle {{ item.qty }}
                td.align-middle.text-right {{ item.final_total | currency}}
            tfoot
              tr
                td.text-right(colspan='3') 
                td.text-right {{ carts.total | currency}}
              tr(v-if='carts.final_total !== carts.total')
                td.text-right.text-success(colspan='3') 
                td.text-right.text-success {{ carts.final_total | currency}}
          .input-group.mb-3.input-group-sm
            input.form-control(type='text', placeholder='', v-model='coupon')
            .input-group-append
              button.btn.btn-outline-secondary.customer-submit(type='button', @click='userCoupon()')
                | 
              button.btn.btn-outline-secondary.customer-code(type='button'
              data-container="body"
              data-toggle="popover"
              data-content="code")
                | 
              button.btn.btn-outline-secondary.customer-ragnarok(type='button'
              data-container="body"
              data-toggle="popover"
              data-content="ragnarok")
                | 
      .my-5.row.justify-content-center
        form.col-md-6(@submit.prevent='createOrder()')
          .form-group
            label(for='useremail') Email
            input#useremail.form-control(type='email', name='email', v-validate="'required|email'", :class="{'is-invalid': errors.has('email')}", v-model='form.user.email', placeholder=' Email')
            span.text-danger(v-if="errors.has('email')") {{ errors.first('email') }}
          .form-group
            label(for='username') 
            input#username.form-control(type='text', name='name', :class="{'is-invalid': errors.has('name')}", v-model='form.user.name', v-validate="'required'", placeholder='')
            span.text-danger(v-if="errors.has('name')") 
          .form-group
            label(for='usertel') 
            input#usertel.form-control(type='tel', name='tel', :class="{'is-invalid': errors.has('tel')}", v-model='form.user.tel', v-validate="'required'", placeholder='')
            span.text-danger(v-if="errors.has('tel')") 
          .form-group
            label(for='useraddress') 
            input#useraddress.form-control(type='address', name='address', :class="{'is-invalid': errors.has('address')}", v-model='form.user.address', v-validate="'required'", placeholder='')
            span.text-danger(v-if="errors.has('address')") 
          .form-group
            label(for='useraddress') 
            textarea.form-control(name='', id='', cols='30', rows='10', v-model='form.message')
          .text-right
            button.btn.btn-danger 
</template>

<style lang="scss" scoped>
  .p-top {
    padding-top: 91px;
  }
  .cart-bg {
    background-image: url(../../assets/img/1378827490-3875360817.jpg);
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    height: 375px;
  }
  .cart-text {
    background-color: rgba(0,0,0,0.6);
    padding: 10px;
  }
</style>

<script>
/* global $ */
export default {
  data() {
    return {
      products: [],
      cacheProducth: {},
      pagination: {},
      carts: [],
      coupon: '',
      isLoading: false,
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: '',
      },
      status: {
        loadingItem: false,
      },
    };
  },
  methods: {
    getCarts() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${
        process.env.COUSTOMPATH
      }/cart`;
      vm.isLoading = true;
      vm.$http.get(url).then((response) => {
        if (response.data.success) {
          vm.carts = response.data.data;
          vm.isLoading = false;
        } else {
          vm.isLoading = false;
          vm.$bus.$emit('message:push',
            `(   |||)
            ${response.data.message}`
            , 'danger');
        }
      });
    },
    userCoupon() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${
        process.env.COUSTOMPATH
      }/coupon`;
      vm.isLoading = true;
      const couponCode = {
        code: vm.coupon,
      };
      vm.$http.post(url, { data: couponCode }).then((response) => {
        if (response.data.success) {
          vm.$bus.$emit('message:push',
            '(*)v'
            , 'success');
          vm.getCarts();
        } else if (response.data.message === '!') {
          vm.isLoading = false;
          vm.$bus.$emit('message:push',
            ';(   |||)'
            , 'danger');
        } else if (response.data.message === '') {
          vm.isLoading = false;
          vm.$bus.$emit('message:push',
            '(   |||)'
            , 'danger');
        }
      });
    },
    removeCart(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${
        process.env.COUSTOMPATH
      }/cart/${id}`;
      vm.status.loadingItem = id;
      vm.$http.delete(url).then((response) => {
        if (response.data.success) {
          vm.$bus.$emit('message:push',
            '(*)v'
            , 'success');
          vm.status.loadingItem = '';
          vm.getCarts();
        } else {
          vm.status.loadingItem = '';
          vm.$bus.$emit('message:push',
            `(   |||)
            ${response.data.message}`
            , 'danger');
        }
      });
    },
    createOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${
        process.env.COUSTOMPATH
      }/order`;
      vm.$validator.validate().then((result) => {
        if (result) {
          vm.$http.post(url, { data: vm.form }).then((response) => {
            if (response.data.message === '') {
              vm.$bus.$emit('message:push',
                '(*)v'
                , 'success');
              vm.$router.push(`/check_order/${response.data.orderId}`);
            } else if (response.data.message === '') {
              vm.$bus.$emit('message:push',
                `(   |||)
                ${response.data.message}`
                , 'danger');
            } else if (response.data.message === '') {
              vm.$bus.$emit('message:push',
                `(   |||)
                ${response.data.message}`
                , 'danger');
            } else if (response.data.message === '') {
              vm.$bus.$emit('message:push',
                `(   |||)
                ${response.data.message}`
                , 'danger');
            } else {
              vm.$bus.$emit('message:push',
                `(   |||)
                ${response.data.message}`
                , 'danger');
            }
          });
        } else {
          this.$bus.$emit('message:push',
            '(   |||)'
            , 'danger');
        }
      });
    },
    popoverBtn() {
      $(() => {
        $('[data-toggle="popover"]').popover();
      });
      $('.customer-code').popover({ placement: 'top' }).click(() => {
        setTimeout(() => {
          $('.customer-code').popover('hide');
        }, 2500);
      });
      $('.customer-ragnarok').popover({ placement: 'top' }).click(() => {
        setTimeout(() => {
          $('.customer-ragnarok').popover('hide');
        }, 2500);
      });
      $('.customer-code').on('show.bs.popover', () => {
        $('.customer-ragnarok').popover('hide');
      });
      $('.customer-ragnarok').on('show.bs.popover', () => {
        $('.customer-code').popover('hide');
      });
      $('.customer-submit').click(() => {
        $('.customer-code').popover('hide');
        $('.customer-ragnarok').popover('hide');
      });
    },
  },
  created() {
    this.getCarts();
  },
  mounted() {
    this.popoverBtn();
  },
};
</script>



